/* Main Tags
==================================== */
html {
  box-sizing: border-box; }

*,
*::before,
*:after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-family: 'Roboto', sans-serif;
  color: #202020;
  background-color: #fafafa;
  font-smooth: always;
  -webkit-font-smoothing: antialised;
  -moz-osx-font-smoothing: grayscale; }

ul {
