      color: gray;
      float: right;
      font-weight: bold;
      font-size: 12px;
      margin-top: 10px; }
  .add-task__main {
    position: relative; }
  .add-task__project, .add-task__date {
    cursor: pointer;
    float: right;
    color: gray;
    margin: 20px 10px; }
  .add-task__project {
